<template>
<!-- 待办详情 -->
  <div class="wait-dealt-details">
    <div class="title">
      <navBar :titleText="$route.query.waitDealtName"></navBar>
    </div>
    <div class="info-body">
      <div v-if="JSON.stringify(info) != '{}'">
  <!-- 以下 ？ 是pc端有但是接口没找到, 看了一下大概率是因为字典 -->
      <div v-if="$route.query.waitDealtName == '方案管理'">
        <van-cell center title="方案类别" :label="schemeCategory" />
        <!-- 方案类别为 项目类方案展示 -->
        <!-- {{info.solutionCategory}} -->
        <div v-show="info.solutionCategory == 2">
          <van-cell center title="项目编码" :label="info.projectCode" />
          <van-cell center title="项目名称" :label="info.projectName" />
          <van-cell center title="项目类型" :label="info.projectType" />
          <van-cell center title="项目类别" :label="info.projectCategory" />
          <van-cell center title="项目等级" :label="info.projectGrade" />
          <van-cell center title="作业类型" :label="info.taskType" />
        </div>
        
        <van-cell center title="直属单位" :label="info.auditOrganization" />
        <van-cell center title="检修装置" :label="info.deviceName" />
        <van-cell center title="方案编号" :label="schemeNo" />
        <van-cell center title="方案名称" :label="info.solutionName" />
        <!-- 方案类型 我看大检修感觉就是写死的 (不对 正在改) -->
        <van-cell center title="方案类型" :label="schemeType" />
        <van-cell center title="方案编制单位" :label="info.solutionOrganizationName" />
        <van-cell center title="计划完成日期" :label="info.planTime" />
        <van-cell center title="实际完成日期" :label="info.finishedTime" />
        <van-cell center title="生效开始日期" :label="info.startTime" />
        <van-cell center title="生效结束日期" :label="info.endTime" />
        <van-cell center title="检修实施单位" :label="info.serviceOrganizationName" />
        <van-cell center title="备注" :label="info.remark" />
      </div>
      <div v-if="$route.query.waitDealtName == '委托设计'">
        <van-cell center title="委托单位" :label="info.delegationPartyShow" />
        <van-cell center title="装置名称" :label="info.deviceIdShow" />
        <van-cell center title="委托项目名称" :label="info.projectName" />
        <!-- ! -->
        <van-cell center title="项目类型" :label="info.projectType" />
        <van-cell center title="委托书编号" :label="info.code" />
        <van-cell center title="设计单位" :label="info.designIdShow" />
        <van-cell center title="设计经理" :label="info.designManager" />
        <van-cell center title="联系电话" :label="info.phone" />
        <van-cell center title="计划投资" :label="info.planInvest" />
        <van-cell center title="资金来源" :label="info.capitalSource" />
        <!-- ! -->
        <van-cell center title="设计阶段" :label="info.designPhase" />
        <!-- ! -->
        <van-cell center title="委托时间" :label="info.delegationTime" />
        <van-cell center title="责任处室" :label="info.responsibilityOfficeIdShow" />
        <van-cell center title="委托依据及理由(前期工作、立项报告、批准文件等)" :label="info.delegationGist" />
        <van-cell center title="委托设计内容和技术条件(含建议方案)" :label="info.delegationContent" />
        <van-cell center title="创建人" :label="info.creator" />
        <van-cell center title="创建时间" :label="info.creationTime" />
        <van-cell center title="修改人" :label="info.modifier" />
        <van-cell center title="修改时间" :label="info.modificationTime" />
      </div>
      <div v-if="$route.query.waitDealtName == '大检修管理手册'">
        <van-cell center title="手册编号" :label="info.manualCode" />
        <van-cell center title="手册名称" :label="info.manualName" />
        <van-cell center title="编制单位" :label="info.orgId" />
        <van-cell center title="手册类型" :label="info.manualType" />
        <van-cell center title="版本类别" :label="info.versionName" />
        <van-cell center title="编制时间" :label="new Date().toLocaleString('zh-CN',{year:'numeric', month:'2-digit',day:'2-digit'}).replace(/\//g, '-')" />
        <!-- 上传文档 展不展示？？？ -->
        <van-cell center title="备注" :label="info.remark" />
      </div>
      <div v-if="$route.query.waitDealtName == '大检修规范化管理' || $route.query.waitDealtName == '检修统筹计划' || $route.query.waitDealtName == '检修专项排查'">
        <van-cell center title="发布人" :label="info.issueUser" />
        <van-cell center title="发布时间" :label="info.issueTime" />
        <van-cell center title="状态" :label="info.status" />
        <van-cell center title="提醒提前量" :label="info.warnNumber + '天'" />
        <van-cell center title="标准分" :label="info.standardScore + '分'" />
        <van-cell center title="工作类型" :label="info.isCirculate" />
        <van-cell v-if="info.isCirculate == '循环性工作'" center title="完成起止时间" :label="info.wancheng" />
        <van-cell v-else center title="要求完成时间" :label="info.endTime" />
        <van-cell center title="循环周期" :label="info.cycleTime" />
        <van-cell center title="工作内容及要求" :label="info.details" />
      </div>
      <div v-if="$route.query.waitDealtName == '非检修项目计划'">
        <van-cell center title="项目编号" :label="info.projectNum" />
        <van-cell center title="项目名称" :label="info.projectName" />
        <van-cell center title="直属单位" :label="info.operationDepartment" />
        <van-cell center title="装置名称" :label="info.deviceId" />
        <van-cell center title="项目类别" :label="info.projectCategory" />
        <van-cell center title="项目类型" :label="info.projectType" />
        <van-cell center title="项目等级" :label="info.projectLevel" />
        <van-cell center title="作业类型" :label="info.jobType" />
        <van-cell center title="承包商名称" :label="info.contractor" />
        <van-cell center title="项目经理" :label="info.projectManager" /> 
        <van-cell center title="专业分类" :label="info.professionalCategory" />
        <van-cell center title="项目进展" :label="info.projectProgress" />
        <van-cell center title="计划开始时间" :label="info.planStartTime ? info.planStartTime.split(' ')[0] : ''" />
        <van-cell center title="计划结束时间" :label="info.planCompletionTime ?  info.planCompletionTime.split(' ')[0] : ''" />
        <van-cell center title="实际开始时间" :label="info.actualStartTime" />
        <van-cell center title="实际结束时间" :label="info.actualCompletionTime" />
        <van-cell center title="监理单位" :label="info.supervisionOrganization" />
        <van-cell center title="是否委托设计" :label="info.commissionedDesign" />
        <van-cell center title="是否施工监理" :label="info.constructionSupervision" />
        <van-cell center title="是否使用吊车" :label="info.craneUse" />
        <van-cell center title="是否检修方案" :label="info.repairPlan" />
        <van-cell center title="委托单位" :label="info.delegateOrganization" />
        <van-cell center title="设计单位" :label="info.designOrganization" />
        <van-cell center title="项目主管部门" :label="info.projectAuthority" />
        <van-cell center title="创建人" :label="info.creator" />
        <!-- 修改人不存在的话 回显 当前登录人 （决定把获取当前人放在vuex里） -->
        <van-cell center title="修改人" :label="info.modifier" />
        <van-cell center title="创建时间" :label="info.createdtime" />
        <van-cell center title="修改时间" :label="info.modifiedtime ? info.modifiedtime.split(' ')[0] : ''" />
        <van-cell center title="主要内容" :label="info.mainContent" />
        <van-cell center title="备注" :label="info.notes" />
        <!-- 还有一个上传的附件不知道要不要展示 -->
      </div>
      </div>
      <div class="float-button" v-show="!btnVisible">
        <van-button type="info" @click="toExamine">
          <img :src="examine">
          <span v-show="!btnTextVisible">{{btnText}}</span>
        </van-button>
      </div>
    </div>
    <van-empty
      v-show="JSON.stringify(info) == '{}'"
      class="custom-image"
      image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
      description="无待办事项"
    />
    <!-- 弹出 选择装置 start -->
    <van-popup
    v-model="show"
    position="bottom"
    :style="{ height: '100%' }"
    >
    <examine ref="examine" @changeClose='changeClose'></examine>
    </van-popup>
    <!-- 弹出 选择装置 end -->
    <examine v-show="false" ref="examine" @changeClose='changeClose'></examine>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import navBar from '@/components/navBar.vue';
import examine from './examine.vue';
export default {
  name: 'WaitDealtDetails',
  components: {
    navBar,
    examine
  },
  data() {
    return {
      show: false,
      info: {a: 12},
      schemeCategory: '',
      schemeNo: '',
      schemeType: '',
      examine: require('@/assets/examine.svg'),
      btnTextVisible: false,
      btnVisible: false,
      btnText: '审核',
      schemeTypeOption: [], //  方案类型
      principal: {},  //  需要传的参数
    }
  },
  methods: {
    toExamine() {
      this.show = true;
      this.$nextTick(()=> {
        if (this.btnText == '审核') {
          this.$refs.examine.examineInit(this.$route.query.waitDealtName, this.$route.query.approvalId, this.info,  this.principal)
        } else {
          if (this.$route.query.modulE != '大检修规范化管理' && this.$route.query.modulE != '检修专项排查' && this.$route.query.modulE != '检修统筹计划' && this.$route.query.modulE != '问题管理') {
            this.$refs.examine.submitInit(this.$route.query.modulE, this.$route.query.approvalId, this.principal);
          } 
        }
      })
    },
    changeClose() {
      this.show = false
    },
    async getDetails(module, businessId) {  //  判断是哪个待办的详情
      this.info = {};
      let res = {};
      console.log('res', res, module, businessId)
      switch (module) {
        case '检修方案':  //  方案管理
          res = await this.$getHttp(`${this.$baseUrl}/OS-PM/${window.localStorage.getItem('versionId')}/solution/getOneSolution/${businessId}`);
          this.info  = res.data;
          console.log('方案管理,,检修方案', res);
          this.principal = {
            '直属单位': this.info.orgName,
						'编制单位': this.info.solutionOrganizationName
          }
          this.programmeManageInit();
        break;

        case '委托设计':
          res = await this.$postHttp(`${this.$baseUrl}/OS-PM/${window.localStorage.getItem('versionId')}/delegation/findById/${businessId}`);
          this.info  = res.data;
         this.info.projectType = res.data.projectType == 'OVERHAUL' ? '检修' : res.data.projectType == 'NOT_OVERHAUL' ? '非检修' : '';
         this.info.designPhase = res.data.designStage == 'PRELIMINARY_DESIGN' ? '初步设计' : res.data.designStage == 'TECHNOLOGY_DESIGN' ? '技术设计' : res.data.designStage == 'DETAIL_DESIGN' ? '施工图设计' : res.data.designStage == 'DETAILED_DESIGN' ? '详细设计' : ''
          console.log('委托设计', res)
          this.principal = {
            '设计单位': this.info.designIdShow,
            '委托单位': this.info.delegationPartyShow,
          }
        break;

        case '问题管理':  //  暂时还没有
        
        break;

        case '检修专项排查':
          this.handleInterface(this.$route.query.waitDealtName)
        break;

        case '检修统筹计划':
          this.handleInterface(this.$route.query.waitDealtName)
        break;
        
        case '检修手册':  // 大检修管理手册 检修手册
          res = await this.$postHttp(`${this.$baseUrl}/OS-PM/${window.localStorage.getItem('versionId')}/manual/getAll`, {contentType: 'application/json;charset=UTF-8'});
          res.data.results.forEach(item=> {
            if (item.id == businessId) {
              this.info = {
                manualCode: item.manualCode, //手册编号
                manualName: item.manualName, //手册名称
                manualType: item.manualType, //手册类型value
                orgId: item.orgId, //单位value
                versionId: item.versionId, //版本类别value
                versionName: item.versionName,  //  版本类别
                remark: item.remark,
                id: item.id,
              }
            }
            
          })
          console.log('大检修管理手册, 检修手册', res)
          this.overhaulManualInit()
        break;

        case '大检修规范化管理':  //  待办里面没有， 问世伟的审批要
          this.handleInterface(this.$route.query.waitDealtName)
        break;

        case '非检修项目计划':
          res = await this.$getHttp(`${this.$baseUrl}/OS-PM/version/${window.localStorage.getItem('versionId')}/bigOverhaulProjectPlan/nonOverhaulProject/${businessId}/detail`);
          this.info = res.data;
          console.log('非检修项目计划', res)
          this.principal = {
            直属单位: this.info.operationDepartment,
            承包商: this.info.contractor,
            监理单位: this.info.supervisionOrganization,
          }
          this.noOverhaulProjectPlanInit()
        break;
        
        case '交检修前单项验收':
        
        break;

        
        case '停工交检修确认单':
          res = await this.$postHttp(`${this.baseUrl}/OS-PM/${window.localStorage.getItem('versionId')}/deviceShutdownPlan/get`);
          console.log('停工交检修确认单', res)
        break;

        case '过程质量检查':
        
        break;

        case '项目完工验收':
        
        break;

        case '交开工前单项验收':
        
        break;

        case '检修叫开工确认单':
        
        break;
      
        default:
          break;
      }
    },
    getBusinessId() {  //  中间接口 为了获取businessId
      this.$getHttp(`${this.$baseUrl}/OS-PM/approvalFlow/approvals/${this.$route.query.approvalId}`).then(res=> {
        if (res.status == 200) {
          this.getDetails(res.data.module, res.data.businessId);
        console.log('getBusinessId', res)
        }
      }).catch(err=> {
        // debugger
      })
    },
    async handleInterface(module) { //  处理共同接口的  检修专项排查、检修统筹计划、大检修规范化管理
      let res = {};
      res  = await this.$postHttp(`${this.$baseUrl}/OS-PM/overhaulManagement/${window.localStorage.getItem('versionId')}/selectWorkTask`, 
        {
          scene: module == '检修专项排查' ? '2' : module == '检修统筹计划' ? '0' : module == '大检修规范化管理' ? '1' : '',
          tag: "feedback",
        }
      );
      res.data.results.forEach(item=> {
        item.itemTasks.forEach(task=> {
          if (task.id == this.$route.query.number) {
            this.info = item;
          }
        })
      })
      this.info.status = this.info.status == 1 ? "已发布" : "未发布";
      this.info.isCirculate = this.info.isCirculate == 1 ? "循环性工作" : "一次性工作";
      this.info.wancheng = this.info.startTime + " 至 " + this.info.endTime;
      console.log(this.$route.query.waitDealtName, res, 'this.info', this.info)
    },


    //  获取方案管理--方案类别
    getSchemeCategory() { //  接口参数看大检修代码是写死的
      this.$getHttp(`${this.$baseUrl}/OS-PM/dict/getAll?enumClass=SolutionParam$SolutionCategory`).then(res=> {
        console.log('getSchemeCategory%%%%ccccmmmm', res, this.info)
        if (res.status == 200) {
          res.data.forEach(item=> {
            console.log('Object.keys(item)[0]', Object.keys(item)[0], 'this.info.solutionCategory', this.info.solutionCategory)
            if (Object.keys(item)[0] == this.info.solutionCategory) {
              this.schemeCategory = Object.values(item)[0];
            }
          })
          this.schemeNo = dayjs(new Date()).format('YYYYMMDDHHmmss')
          console.log('this.info.schemeNo', this.schemeNo, 'this.schemeCategory', this.schemeCategory)
        }
      })
    },
    // 获取方案管理--方案类型
    getFindAllSolutionType() {
      this.$getHttp(`${this.$baseUrl}/OS-PM/${window.localStorage.getItem('versionId')}/solution/findAllSolutionType`).then(res=> {
        console.log('findAllSolutionType', res, this.info)
        if (res.status == 200) {
          this.schemeTypeOption = res.data;
          let key = Object.keys(this.schemeTypeOption[parseInt(this.info.solutionCategory) - 1][0])[0]
          this.schemeType = this.schemeTypeOption[parseInt(this.info.solutionCategory) - 1][0][key]
        }
        console.log('this.schemeType', this.schemeType)
      })
    },
    //  检修手册--编制单位
    getPreparedBy() {
      this.$postHttp(`${this.$baseUrl}/OS-PM/organization/getOrg`, {
        isFormData: true
      }).then(res=> {
        console.log('getPreparedBy', res, this.info)
        if (res.status == 200) {
          res.data.forEach(item=> {
            if (item.id == this.info.orgId) {
              this.info.orgId = item.orgName 
            }
          })
        }
        console.log('this.getPreparedBy', this.info)
      })
    },
    //  检修手册--手册类型列表
    getManualType() {
      this.$getHttp(`${this.$baseUrl}/OS-PM/dict/getAll?enumClass=BaseData$ManualType`).then(res=> {
        console.log('getManualType', res, this.info)
        if (res.status == 200) {
          res.data.forEach(item=> {
            if (Object.keys(item)[0] == this.info.manualType) {
              this.info.manualType = Object.values(item)[0] 
            }
          })
        }
        console.log('this.getManualType', this.info)
      })
    },    
    programmeManageInit() {
      this.getSchemeCategory();
      this.getFindAllSolutionType();
    },
    overhaulManualInit() {
      this.getPreparedBy()
      this.getManualType()
    },

    // 非检修项目计划--直属单位、装置名称
    noOverhaulProjectPlanInit() {
      this.$getHttp(`${this.$baseUrl}/OS-PM/version/getListVersionAndDeviceAll`).then(res=> {
        console.log('noOverhaulProjectPlanInit', res, this.info)
        if (res.status == 200) {
          res.data.forEach(item=> {
            if (item.id == window.localStorage.getItem('versionId')) {
              item.deviceList.forEach(ele=> {
                if (ele.id == this.info.deviceId) {
                  this.info.operationDepartment = ele.orgName
                  this.principal.直属单位 = ele.orgName
                  this.info.deviceId = ele.deviceName
                }
              })
            }
          })
        }
        console.log('this.noOverhaulProjectPlanInit', this.info)
      })
    },
    async init(approvalId) {
      let res = await this.$getHttp(`${this.$baseUrl}/OS-PM/approvalFlow/approvals/${approvalId}`)
      console.log('waitDealtDetails--init', res)
      if (res.data.state == 'DISAGREE_STATE') { //  回退审批
        this.btnText = '回退重审'
      }
    },
  },
  mounted() {
    // 方案管理（有附件）现在页面没做显示
    

    /*
      审批ok：方案管理、
      审批确定待确认： all
    */ 

    this.init(this.$route.query.approvalId);


    /* 三合一的待办确认不跳转到此页面，以下代码不会执行 */ 
    // 大检修规范化管理   检修统筹计划   检修专项排查 为三合一页面
    if (this.$route.query.waitDealtName == '大检修规范化管理' || this.$route.query.waitDealtName == '检修统筹计划' || this.$route.query.waitDealtName == '检修专项排查') {  //  不需要调用中间接口
      this.btnVisible = true;
      this.getDetails(this.$route.query.waitDealtName, null)
    } else {
      this.getBusinessId();
    }
    
    let timeoutHdr = null
    document.querySelector('.info-body').addEventListener('scroll', () => {
      // 开始滚动时执行的代码
      console.log('cnm')
      this.btnTextVisible = true;
      window.clearTimeout(timeoutHdr)
      timeoutHdr = window.setTimeout(() => {
        // 滚动停止时执行的代码
        this.btnTextVisible = false;
      }, 101)
    })

  }
}
</script>

<style lang='less' scoped>
  * {
    padding: 0;
    margin: 0;
  }
  .wait-dealt-details {
    height: 100%;
    overflow: hidden;
    box-sizing: border-box;
    padding: 0rem 1rem;
    padding-right: 0rem;
    .info-body {
      height: calc(100vh - 4.375rem);
      overflow: auto;
      .van-cell {
        margin: 1rem 0rem;
      }
      .info-name {
        .van-cell__title {
          font-size: 1.5rem;
          font-weight: 500;
        }
      }
      .van-cell__title {
        font-size: 1rem;
        .van-cell__label {
          font-size: 0.875rem;
          margin-top: 0.25rem;
        }
      }
    }
    .float-button {
      position: fixed;
      right: 1rem;
      bottom: 1rem;
      .van-button {
        border-radius: 1rem;
        font-size: 0.875rem;
        background: #0247fe;
        padding: 1rem;
        img {
          vertical-align: middle;
          margin-right: 0.25rem;
        }
      }
    }
    .van-empty {
      height: calc(100vh - 5rem);
    }
  }
</style>